@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
	margin: 0;
	font-family: 'Roboto', georgia, serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	background: #131313;
}

.container {
	width: 95%;
	min-width: 350px;
	max-width: 1500px;

	height: 100%;
	min-height: 500px;
	max-height: 1500px;

	margin: 0px auto;
	padding: 20px 0px;
	box-sizing: border-box;

	font-size: 30px;
}

@media only screen and (max-width: 600px) {
	.container {
		font-size: 25px;
	}
}

.calendar {
	display: flex;
	flex-direction: column;
	border-radius: 10px;
	border: 2px solid black;
	overflow: hidden;
	box-sizing: border-box;
	height: 100%;
	background: #faf9fa;
}

.btn {
	padding: 10px 30px;
	box-sizing: border-box;
	margin: auto;
}

.header {
	box-sizing: border-box;
	text-align: center;
	margin: 0px;
	border-left: 1px solid black;
}

.title {
	font-size: 40px;
	padding: 20px;
}

.clickable-title {
	font-size: 40px;
	cursor: pointer;
	padding: 30px 0px;
	margin-top: 20px;
}

.header button {
	background: none;
	border: 1px solid black;
	border-radius: 5px;
	padding: 5px 10px;
}

.all-months {
	background: #f0f0f0;
	width: 100%;
	margin-top: 20px;
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	height: 0px;
	overflow: hidden;
}

.all-months li {
	list-style-type: none;
	color: #bdbdbd;
	padding: 20px;
	font-size: 20px;
	cursor: pointer;
	width: 10%;
}

.grow {
	height: auto;
}

/* .day-labels {
} */

.day-labels ul {
	margin: 0px;
	padding: 0px;
	display: flex;
	flex-direction: row;
	margin: 0px;
	padding: 0px;
	width: 100%;
	box-sizing: border-box;
}

.day-labels li {
	list-style-type: none;
	width: calc(100% * (1 / 7));
	font-size: 14px;
	padding-top: 20px;
	padding-bottom: 5px;
	box-sizing: border-box;
}

.mood-labels,
.mood-labels ul {
	width: 100%;
	margin: 0px;
	padding: 0px;
}

.mood-labels ul {
	display: flex;
	width: 100%;
	flex-direction: row;
	border-bottom: 1px solid black;
}

.mood-labels li {
	list-style-type: none;
	margin: 0px;
	padding: 5px 0px;
	width: 25%;
	font-size: 16px;
}

@media only screen and (max-width: 600px) {
	.all-months li {
		width: 50%;
	}
}

.days {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	border: 1px solid black;
	border-top: none;
}

.single-day,
.not-day {
	border-top: 1px solid black;
	border-right: 1px solid black;
	width: calc(100% * (1 / 7));
	height: calc(100% * (1 / 5));
	box-sizing: border-box;
}

.single-day {
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}

.currentDay {
	/* background: #d0e7ba; */
	border-bottom: 3px solid rgb(255, 94, 0);
}

.login {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.login-message {
	color: #ffffff;
	text-align: center;
	padding: 30px;
}

.login-btn {
}

.modal-container {
	background: rgba(0, 0, 0, 0.9);
	position: fixed;
	top: 0px;
	left: 0px;
	width: 100vw;
	height: 100vh;
	box-sizing: border-box;

	display: flex;
	align-items: center;
	justify-content: center;
}

.modal {
	position: relative;
	padding: 20px;
	box-sizing: border-box;
	background: #202020;
	border-radius: 5px;
	max-width: 500px;
	min-height: 300px;
	width: 95%;
	color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.modal-btn {
	position: absolute;
	top: 10px;
	right: 10px;
	color: white;
	border: none;
	background: none;
	cursor: pointer;
	font-size: 30px;
}

.modal-content {
	display: flex;
	flex-direction: column;
	width: 80%;
}

.modal-content p {
	text-align: center;
	pointer-events: none;
}

.message {
	font-size: 16px;
	line-height: 22px;
	padding: 20px;
	border-radius: 5px;
	outline: none;
	border: none;
	cursor: pointer;
	background: #faf9fa;
	color: black;
}

.mood-circles {
	display: flex;
	width: 100%;
	justify-content: space-between;
	padding: 30px 0px;
	box-sizing: border-box;
}

.mood-circles button {
	width: 90px;
	height: 90px;
	border-radius: 50%;
	border: none;
	font-size: 14px;
	cursor: pointer;
	outline: none;
}

.clear-btn {
	width: 100px;
	background: white;
	border: none;
	border-radius: 5px;
	padding: 10px;
	outline: none;
	cursor: pointer;
}

@media only screen and (max-width: 600px) {
	.mood-circles button {
		width: 70px;
		height: 70px;
		font-size: 12px;
	}

	.modal {
		font-size: 25px;
	}

	.modal-btn {
		font-size: 14px;
	}
}

.mood1 {
	background-color: #8888af;
	color: #2b2a4d;
}
.mood2 {
	background-color: #88a2af;
	color: #2a3f4a;
}
.mood3 {
	background-color: #a5af88;
	color: #4d5638;
}
.mood4 {
	background-color: #c2a255;
	color: #64481c;
}

